import { mapState } from 'vuex'
export default {
    name: 'ArticleMeta',
    props: {
        article: {
            type: Object,
            require: true
        }
    },
    methods: {
        articleClick (type) {
            let val = {
                type: type,
                data: ''
            }
            if (type === 1) {
                // 关注作者
                val.data = this.article.author.username
            } else if (type === 2 || type === 3) {
                // 2 文章点赞 3 删除文章
                val.data = this.article.slug
            }
            this.$emit('click', val)
        }
    },
    computed: {
      ...mapState(['user'])
    }
}
